import React, { useState, useEffect } from 'react'
import "./Post_Core.css"
import { db } from "../firebase"
import {ReactComponent as ThumbUp} from "../images/thumbUp.svg"
import {ReactComponent as Save} from "../images/save.svg"
import {ReactComponent as Back} from "../images/back.svg"
import useFitText from "use-fit-text";
import firebase from "firebase"
import {Avatar} from "@material-ui/core" 

function Post_Core({user, setTechAnim, liked, setLiked, hidden, setHidden, postId, username, imageUrl, content, caption, likecounter, setLikecounter,
    setPostshidden }) {

    const { fontSize, ref } = useFitText();
    const [comments, setComments] = useState([])
    const [comment, setComment] = useState('')
    const [big, setBig] = useState(false);

    var lister = []
  
    var i=0
    for (i; i < 65; i++){
      lister.push("")
    }

    useEffect(() => {
        let unsubscribe;
        if (postId) {
            unsubscribe = db
                .collection("posts")
                .doc(postId)
                .collection("comments")
                .orderBy('timestamp', 'desc')
                .onSnapshot((snapshot) => {
                    setComments(snapshot.docs.map((doc) => doc.data()));
                });
        }
        return () => {
            unsubscribe();
        };
    }, [postId]);

    const postComment = (e) => {
        e.preventDefault();
        db.collection("posts")
            .doc(postId)
            .collection("comments")
            .add({
                timestamp: firebase.firestore.FieldValue.serverTimestamp(),
                // username: username,
                text: comment,
                username: user.displayName
            });
        setComment('');
    console.log(comments)
    console.log(postId)

    }
    const hide = (e) => {
        setHidden(true);
        setPostshidden(false);
        setTechAnim(false);
    }
    const likedFunction = (e) => {
        e.preventDefault();
        setLiked(!liked)
        if(liked === false)
        setLikecounter(counter => counter + 1)
        else
        setLikecounter(counter => counter + -1)
            
    }

    const bigImage = (e) => {
        e.preventDefault()
        setBig(!big)
    }
    return (
        <div>
            <div className={`content__core ${hidden ? "contentIndex" : "unhidden contentIndex"}`}>
         
            
                <div className={`fullview ${big ? "fullview__showing" : ""}`}
                    onMouseUp={() => setBig(false)}>

                    <div className="fullview__image__container"
>
                        <img src={imageUrl} alt="" className="fullview__image"/>
                    </div>
                </div>
                
                <div className="content__core__body">
                   <div className="ccb__linear__background"></div>
                   
                    <div className="ccb__elements">
                    <div className="header"  ref={ref} style={{fontSize}}>
                        <div className="header__title" >
                            <h1 className="core__caption core__text">{caption}</h1>
                        </div>
                        <div className="header__buttons">
                            <Save className={`img__button`}/>
                            <ThumbUp className={`img__button 
                                                ${liked ? "like__active" : ""}`}
                                onClick={likedFunction} />
                            <h1>{likecounter}</h1>

                        </div>
                        
                    </div>
                    
                    {
                        imageUrl ? 
                            (<div className="core__image__container"
                            >
                                <img src={imageUrl} alt="" className="core__image"
                                    onMouseUp={bigImage}
                                />
                            </div>)
                        :""
                        }
                        














                    <h1 className="core__content core__text">{content}</h1>
                        <div className="comment__section">
                            <div className="post__comments">
                                {comments.map((comment) => (
                                
                                <div className="individual__comment">
                                        <Avatar className="core__avatar"/>
                                        <div className="core__user_et_comment">
                                            <h1>{comment.username}</h1>
                                            <h1 className="core__comment">{comment.text}</h1>
                                        </div>
                                    </div>))}
                            </div>
                            <form className="comment__box">
                                <input
                                    className="comment__input"
                                    type="text"
                                    placeholder="Add a comment"
                                    value={comment}
                                    onChange={(e) => setComment(e.target.value)}/>
                                <button
                                    className="comment__button"
                                    disabled={!comment}
                                    type="text"
                                    onClick={postComment}>
                                    comment</button>
                            </form>
                        </div>
                    </div>
                </div>
                <Back className="exit__button" onMouseUp={hide} />
                
            
            </div>

            </div>
            
    )
}

export default Post_Core
