import React, {useState, useEffect} from "react";
import "./App.css";
import { auth, db } from "./firebase";
import ModalComp from "./Components/Modal_Comp"
import Posts from "./Components/Posts";
import EyeOfHorus from "./Components/EyeOfHorus";

function App() {
  const [open, setOpen] = useState(false);
  const [posts, setPosts] = useState([]);
  const [openSignIn, setOpenSignIn] = useState(false);
  const [username, setUsername] = useState('');
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true)
  const [techanim, setTechAnim] = useState(false)


  const [eyeopen, setEyeOpen] = useState(false);


  var lister = []
  
  var i=0
  for (i; i < 121; i++){
    lister.push("")
  }

  useEffect(() => {
    const unsubscribe = auth.onAuthStateChanged((authUser) => {
      if (authUser) {
        setUser(authUser);
      }
      else {
        setUser(null);
      }
    })

    return () => {
      unsubscribe();

    }

}, [user, username]);


  useEffect(() => {
    db.collection('posts').orderBy('timestamp', 'desc')
      .onSnapshot(snapshot => {
      setPosts(snapshot.docs.map((doc) => ({
        id: doc.id,
        data: doc.data()
      })));
    })

  }, []);

  // var lister=[for(i of 10) i + 10]

  

  return (
    <div className="App">
      <div className="App__background"></div>
      <div className="entirety">
        <div id="bgtop" className={`techno__background ${techanim ? "techanimtop" : ""}`}>
        
        {
            lister.map((i) => (
              <div key={i.id} className="box"></div>
              ))
            }
      <div className="fader"></div>
      </div>
        <div id="bgbottom" className={`techno__background ${techanim ? "techanimbottom" : ""}`}>
          {
            lister.map((o) => (
              <div key={o.id} className="box"></div>
              ))
          }
          <div className="fader"></div>
        </div>

      </div>    
      <ModalComp open={open} setOpen={setOpen}
        openSignIn={openSignIn} setOpenSignIn={setOpenSignIn}
        username={username} setUsername={setUsername}
        user={user} setUser={setUser}
        setLoading = {setLoading}
      />
      
      {
        loading ? "" :
        (user ? (
          
          <div className={`outer ${techanim ? "appvision" : ""}`}>
            <button className= "logout" onClick={() => auth.signOut()}>LOGOUT</button>
              <EyeOfHorus posts={posts} setPosts={setPosts} username={user.displayName} user={user.displayName}
                eyeopen={eyeopen} setEyeOpen={setEyeOpen}
              />
          </div>
        ) : (
          <div className={`signer  ${techanim ? "appvision" : ""}`}>
            <button className="inner__signer" onClick={() => setOpenSignIn(true)}>SIGN IN</button>
            <button className="inner__signer" onClick={() => setOpen(true)}>SIGN UP</button>
          </div>))
      }
      
      
      <Posts posts={posts} user={user}
        loading={loading} setLoading={setLoading}
        techanim={techanim} setTechAnim={setTechAnim}
        eyeopen={eyeopen} setEyeOpen={setEyeOpen}
        />

      </div>
  );
}

export default App;
